.c-info {
    width: 100%;
    max-height: 100vh;
    min-height: 100vh;
    margin-bottom: 12%;



    &__container {
        width: 100%;

        display: flex;
        align-items: center;
        justify-content: space-evenly;  
        
    }

    &__item {
        text-align: center;
        width:  33.33%;
        ul{
            list-style-type: none;
            padding: 0;
            margin: 0;
            li{
             
                padding: 16% 0;

                img{
                    width: 50px;
                    margin-bottom: 10px;
                }
                text-transform: uppercase;
            }
        }
        
    }

    &__item--left {
    }

    &__item--image {
        img { 
            width: 100%;
            margin-top: 10vh;
        }
    }

    &__item--right {
    }

}
.c-info h2 {
    text-align: center;
    padding: 0 15% 6% 15%;
    position: relative;
}

.bacround-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    opacity: .1;
}

.col-4 {
}
.img-detect {
}
.is-inview {
    overflow: hidden;
}



@media(max-width: 768px) {
    .c-info {
        width: 100%;
        max-height: 70vh;
        min-height: 450px;
        margin-bottom: 3%;

        h2 {
            padding: 0 8% 6% 8%;

        }
    
    }

    
}

@media(max-width: 480px) {
    .c-info {

      
        &__item--image {
            min-width: 45vw;
        }
        &__item{
            
            li{
                img{
                    width: 34px !important;
                }
            }
            
        }
    }

}