.reviews {
    padding: 0 var(--padding-side);
    display: flex;
    max-height: 90vh;
    min-height: 800px;
    margin-bottom: 20px;

    &__col {

    
        &--left {
            width: 25%;
            margin-right: 7vw;
        }
        
        &--right {
            width: 75%;
            height: 100%;
            max-width: 1300px;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            padding: 0 0vw;
        }
    }
}

.reviews-list {
    list-style: none;
    margin: 0;
    padding: 0;
    


    &__item {
        padding: 3vh 0 12%;
        img{
            width: 120px;
            margin-bottom: 15px;
        }

        span{
            display: block;
            padding-top: 10px;
            opacity: .6;
        }

    }
}
