}
.c-beforeafter {
    width: 45%;
    align-self: stretch;
   
    &__img-over{
        overflow: hidden;
        height: 75%;
        margin-bottom: 25px;
        img {
            width: 100%;
            height: 100%;
    
        }
    }

 
    &__text{
        span{
            display: block;
            padding-top: 10px;
            opacity: .6;
        }

    }


    &--left {
        

    }

    &--right {
        // margin-top: 15vh;
        // width: 33.33%;
        
}
}





    
@media(max-width: 768px) {
    .reviews {
    max-height: 900px;
    min-height: 900px;
    }
    
}

@media(max-width: 480px) {
    .reviews {
        max-height: 140vh;
        
        margin-bottom: 12%;
        &__col{
            &--right {
            flex-direction: column;
            }
            &--left{
                width: 55%;
            }
        
            h2{
                margin: 0;
            }
        }
    }
    .c-beforeafter {
        align-self: flex-end;
        width: 100%;

    }
    .reviews-list__item{
        padding: 0;
        margin: 0;
        p{
            margin: 0 0 35px 0;
        }
    }
}