.how-work{
    display: flex;
    justify-content: space-between;
    padding: 0 var(--padding-side);
    width: 100%;
    // max-height: 100vh;
    // min-height: 1350px;
    margin-bottom: 9%;

    &__sm{
        min-width: 200px;
        width: 25%;     
        margin: auto 0;  

    }
    &__bg{

    }
}

.inview{
    &__title{
        margin: 0;
    }
    &__img{
        overflow: hidden;
        
        img{
            width: 100%;
            margin-top: 50px;
        }
    }
}


.how-steps{
    padding-left: 7vw;

    ul{
        margin: 0;
        padding: 0;
        list-style-type: none;

        li{
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            padding: 30px 0;
            border-bottom: var(--border);
        }
    }
    p{
        width: 30%;
    }

}


@media(max-width: 768px) {
    .how-work {
        max-height: 100vh;
        min-height: 780px;
    }
    
}

@media(max-width: 480px) {
    .how-work {
        &__sm{
            min-width: 150px;
        }
    }

    .how-steps{

        ul{
    
            li{
                flex-wrap: wrap;
                padding: 0;
            }
        }
        p{
            width: 100%;
        }
    }

}