.nav {
    z-index: 100;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    padding-left: 4vw;
    // background-color: black;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.01);
    background: var(--bg-color);

    &__logo {
        margin-right: auto;
    }

    &__list {
        width: 50%;
        margin: auto !important;
    }

    &__buttons {
        display: flex;
        align-items: center;
        a{
            padding-right: 10px;
            white-space: nowrap;
        }
    }
    
}

.nav-list {
    display: flex;
    justify-content: space-evenly;
    padding: 0;
    margin: 0;

    &__item {
        list-style: none;
    }

    &__link {
        text-decoration: none;
        text-transform: uppercase;
        font-size: 12px;
        color: rgba(39, 39, 39, .9);
        &:hover{
            text-decoration: revert;
        }
    }
}

.btn-menu{
    position: relative;
    width: 30px;
    span{
        position: absolute;
        
        display: block;
        width: 100%;
        height: 3px;
        background-color: var(--accent-color);
        border-radius: 15px;

        &:first-child {
           
            top: -10px }

      

        &:nth-child(3){
               
                top: 10px;}
    }
}

.btn-menu.oppen {
    span{
        transition: 0.3s all ease;

        &:first-child {
            transform: rotate(45deg);
            top: 0px;}

        &:nth-child(2) {
            opacity: 0;
            transform: translateX(-100%);}

        &:nth-child(3){
                transform: rotate(-45deg);
                top: 0px;}

    }
    


        
    
    

}

@media(max-width: 768px) {
    .nav {
        &__logo {
            margin-left: auto;
        }

    }

    .nav-list {
        padding-top: 5px;
        width: 100%;
        height: 100%;
        position: fixed;
        top: 70px;
        left: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        background-color: var(---bg-color);
        background: rgba(255, 164, 114, 0.5);
        backdrop-filter: blur(2.5px);
        &__item{
            padding: 10px 0;
        }

    }



    .nav-list__item .nav-list__link{
        font-weight: 600;
        font-size: 22px;
    }
    
}
